import * as React from 'react';
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import {
  Link
} from "react-router-dom";


import { ics } from 'ics-file-export';


const exportICS = (title, description, start, end) => {
    const newEvent = { title, description, start,end }
    // Create the .ics URL
    let url = [
      "BEGIN:VCALENDAR",
      "VERSION:2.0",
      "BEGIN:VEVENT",
      "PRODID:-//hacksw/handcal//NONSGML v1.0//DE",
      "DTSTART:" + newEvent.start,
      "DTEND:" + newEvent.end,
      "SUMMARY:" + newEvent.title,
      "DESCRIPTION:" + newEvent.description,
      "LOCATION:" + 'Paradiesstrasse 13, 9000 St. Gallen',
      'UID:19970610T172345Z-AF23B2@example.com',
      "BEGIN:VALARM",
      "TRIGGER:-PT15M",
      "REPEAT:1",
      "DURATION:PT15M",
      "ACTION:DISPLAY",
      "DESCRIPTION:Reminder",
      "END:VALARM",
      "END:VEVENT",
      "END:VCALENDAR"
    ].join("\n");
  
    let blob = new Blob([url], { type: 'text/calendar;charset=utf-8' });
  
    if (/msie\s|trident\/|edge\//i.test(window.navigator.userAgent)) {
      // Open/Save link in IE and Edge
      window.navigator.msSaveBlob(blob, `.ics`);
    } else {
      // Open/Save link in Modern Browsers
      window.open(encodeURI("data:text/calendar;charset=utf8," + url));
    }
  
  }


const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

function createData(time, programm, exportLink, start, end) {
  return { time, programm, exportLink, start, end };
}

const rows = [
  createData( '10:00 bis 14:00', 'Brunch im Garten oder drinnen', 'in Kalender exportieren', '20220813T100000', '20220813T140000'),
  createData( '14:00 bis 18:00', 'Dessert im Garten oder drinnen', 'in Kalender exportieren','20220813T140000', '20220813T180000'),
  createData( '17:00 bis 22:00', 'Grillieren im Garten oder drinnen', 'in Kalender exportieren','20220813T170000', '20220813T220000'),
  createData( 'ca. 22:00 bis offen', 'ab in die Stadt','in Kalender exportieren','20220813T220000', '20220814T100000'),
];

export default function Wann() {
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 300 }} aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell>Zeit</StyledTableCell>
            <StyledTableCell align="left">Programm</StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <StyledTableRow>
              <StyledTableCell align="left"onClick={() => exportICS('Jan 30er Geburtstag', row.programm, row.start, row.end)}><Link>{row.time}</Link></StyledTableCell>
              <StyledTableCell align="left">{row.programm}</StyledTableCell>
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

